.button {
  display: flex;
  justify-content: center;
  align-items: center;
  
  width: 100%;
  height: 56px;

  font-weight: 700;
  font-size: 16px;
  line-height: 27px;

  color: $color-dark;
  background: $white-color;
  border-radius: 40px;
  cursor: pointer;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }
}
