.icon {
  position: relative;

  display: block;
  width: 100%;
  padding-bottom: 100%;
  
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;

  &__tooltip {
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%);

    
    width: max-content;
    padding-right: 20px;

    font-size: 12px;
    line-height: 16px;
    color: $white-color;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: opacity 0.3s;
    opacity: 0;
    pointer-events: none;
    
  }

  @include onDesktop {
    &:hover &__tooltip {
      opacity: 1;
      pointer-events: all;
    }
  }

  &--menu {
    background-image: url(../images/menu.svg);

    &:hover {
      background-image: url(../images/menu_hover.svg);
    }
  }

  &--phone {
    background-image: url(../images/phone.svg);
  }

  &--cross {
    background-image: url(../images/crossDark.svg);
  }
}
